import React from 'react'
import {CurrencyDataType} from '../../../types/types'
import s from './CurrencyData.module.scss'

type CurrencyDataPropsType = {
    curData: CurrencyDataType
}

export function CurrencyData({curData}: CurrencyDataPropsType) {
    const {
        Cur_ID,
        Date,
        Cur_Abbreviation,
        Cur_Scale,
        pastData,
        Cur_Name,
        Cur_OfficialRate
    } = curData

    const clearDate = Date.split('T')[0].replace(/-/g, '.')

    return (
        <div className={s.currency_data}>

            <div className={s.currency_row}>
                <h4 className={s.currency_label}> :</h4>
                <span className={s.currency_value}>{Cur_Name}</span>
            </div>

            <div className={s.currency_row}>
                <h4 className={s.currency_label}>   (id):</h4>
                <span className={s.currency_value}>{Cur_ID}</span>
            </div>

            <div className={s.currency_row}>
                <h4 className={s.currency_label}>   {clearDate}  :</h4>
                <span className={s.currency_value}>{Cur_Scale} {Cur_Abbreviation} - {Cur_OfficialRate} BYN</span>
            </div>

            <div className={s.currency_row}>
                <h4 className={s.currency_label}>{pastData.Date}  :</h4>
                <span
                    className={s.currency_value}>{Cur_Scale} {Cur_Abbreviation} - {pastData.Cur_OfficialRate} BYN</span>
            </div>

        </div>
    )
}