export function subtractWorkday(date: string) {
    const x = new Date(Date.parse(date.split('T')[0]))

    const dayOfWeek = x.getDay() //     0 ()  6 ()
    let daysToSubtract = 1 //    1 
    if (dayOfWeek === 0) { //  ,   2 
        daysToSubtract = 2
    } else if (dayOfWeek === 1) { //  ,   3 
        daysToSubtract = 3
    }
    const newDate = new Date(x)
    newDate.setDate(x.getDate() - daysToSubtract) //    
    return newDate.toISOString().slice(0, 10) //     YYYY-MM-DD
}